.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

/* Ensure the layout displays sidebar and main content side by side */
/* Ensure the layout displays sidebar and main content side by side */
.app-layout {
  display: flex;
  height: 100vh; 
  width: 100vw; 
  overflow: hidden;
}

/* Sidebar styles */


/* Main content styles */
.main-content {
  margin-left: 240px; 
  padding: 20px;
  width: calc(100% - 240px); 
  background-color: #ffffff;
  overflow-y: auto;
}




.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
